import React, { useContext } from 'react';
import classNames from 'classnames';
import { JdCategoryContext } from './context';
var selectorPrefix = 'adhere-ui-jdcategorytab';
var JdCategoryTabItem = function (props) {
    var id = props.id, children = props.children, className = props.className, style = props.style;
    var activeKey = useContext(JdCategoryContext).activeKey;
    return (React.createElement("li", { className: classNames(selectorPrefix + "-tab-item", activeKey === id ? 'active' : null, className || ''), style: style || {} }, children));
};
